@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.servicios {
    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        gap: 2rem;
        @include m.tablet {
            flex-direction: row;
        }
    }
    &__card {
        padding: 2rem;
        background-image: radial-gradient(circle, v.$primario 0, #8be8c9 100%);
        color: v.$secundario;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        gap: 2rem;
        text-align: center;
        @include m.tablet {
            width: 25%;
            height: 50rem;
        }
    }
    .basico {
        border-radius: 4rem 0 4rem 0;
        border: 4px solid v.$secundario;
    }
    .estandar {
        background-image: radial-gradient(circle, v.$gris 0%, v.$secundario 100%);
        color: v.$blanco;
        border-radius: 0 0 4rem 4rem;
        border: 4px solid v.$negro;
        @include m.tablet {
            padding-top: 4rem;
            height: 54rem;
        }
    }
    .premium {
        border-radius: 0 4rem 0 4rem;
        border: 4px solid v.$secundario;
    }
    &__imagen {
        
    }
    &__img {
        width: 10rem;
        margin: 0 auto;
    }
    &__titulo {
        font-weight: 400;
    }
    &__listado {
        list-style: none;
        padding: 0;
        margin: 0;
    }
    &__texto {
        font-size: 2rem;
        padding: 0;
        margin: 0;
        margin-bottom: .5rem;
    }
    &__precio {
        font-size: 4rem;
        font-weight: 900;
    }
}